html{
  font-size: 16px;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

body{
  background-color: #ceddd4;
}

.App{
  max-height: 100vh;
}

h1{
  margin: 1rem auto;
  width: 70%;
  font-size: 4rem;
  text-align: center;
  color: #506a00;
}

.wrapper{
  width: 70%;
  margin: auto;
  display: grid;
  place-items: center; 
}

.wrapper > *{
 margin-block: .6em;
}

.ques{
  background-color: #eefff8;
  padding: 2rem 2rem;
  font-size: 1.3rem;
  border-radius: 10px;
}

.options{
  width: 90%;
  display: grid;
  justify-items: center;
}
.options > *{
  margin-block: .5em;
 }

.op{
  display: flex;
  background-color: #eefff8;
  width: 80%;
  justify-content: center;
  position: relative;
  padding-block: 1.5rem;
  border-radius: 10px;
  cursor: pointer;
}

.op span {
  position: absolute;
  left: 1em;
  top: 27%;
  font-size: 1.5rem;
}

.op p{
  font-size: 1.3rem;
}
.wrong {
  background-color: red;
}
.correct{
  background-color: green;
}


button{
  display: none;
  background-color: rgb(69, 69, 252);
  color: white;
  border: none;
  padding: 10px 2rem;
  border-radius: 5px;
}

.visible{
  display: block;
}


/* Points */

h2{
  font-size: 5rem;
}

.word{
 font-size: 2rem;
}


/* Responsive baby */

@media (max-width: 700px){

  html{
    font-size: 14px;
  }

  h1{
    width: 80%;
  }
  .wrapper{
    width: 90%;
  }

}
@media (max-width: 570px){

  html{
    font-size: 12px;
  }
}
@media (max-width: 435px){

  .options{
    width: 100%;
  }
  .op{
    width: 90%;
  }
}

@media (max-width: 320px){
  html{
    font-size: 11px;
  }
}